<template>
  <div ref="vantaRef" class="vanta-container">
    <div class="login-container">
      <!-- LOGO -->
      <div class="header">
        <img src="@/assets/.png" alt="logo" />
        <span class="title"></span>
        <span class="sub-title"></span>
      </div>

      <!--  -->
      <div class="info-container">
        <div v-if="showText" :class="['animate__animated', currentAnimation]" ref="textRef">
          <i :class="InfoIcon" style="font-size: 0.8rem;margin-right: 0.3rem; color: #FF412C;"></i>
          <span class="info-text" style="font-size: 12px; font-weight: 600; color: #FF412C;">{{ TextContent }}</span>
        </div>
      </div>

      <!--  -->
      <form @submit.prevent="login">
        <div class="body">
          <!--  -->
          <div class="form-group">
            <label class="form-lable" for="username"></label>
            <InputText class="form-input" type="text" id="username" v-model="username" required />
          </div>

          <!--  +  -->
          <div class="form-group">
            <div class="form-operation">
              <label class="form-lable" for="password"></label>
              <span
                style="text-align: right;color: #9EF1A1; font-size: 12px; letter-spacing: 1px;text-decoration: underline; cursor: pointer;"
                @click="handleforgetPassword()"></span>
            </div>
            <InputText class="form-input" type="password" id="password" v-model="password" required />
          </div>

          <!--  -->
          <div class="form-group">
            <div class="remember-wrapper">
              <!-- CheckboxrememberUser -->
              <Checkbox 
                class="checkbox" 
                v-model="rememberUser" 
                inputId="remember" 
                name="remember" 
                value="remember"
                :binary="true"
                size="small"
                style="margin: 0;" 
              />
              <!-- Checkbox -->
              <label 
                for="remember"
                class="remember-label"
              >
                
              </label>
            </div>
            <!--  -->
            <Button 
              label="" 
              raised 
              @click="login" 
              style="background-color: #202020; border: none; width: 100%; margin-top: 0.5rem;" 
            />
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount, watch } from 'vue'
import { useRouter } from 'vue-router';
import axiosInstance from '../axiosConfig.js';
// PrimeVue
import InputText from 'primevue/inputtext';
import Button from 'primevue/button';
import Checkbox from 'primevue/checkbox';
import 'primeicons/primeicons.css'
// Animate.css
import 'animate.css';


// 3D
import * as THREE from 'three';
import NET from 'vanta/src/vanta.net.js';

const router = useRouter();

// 1. 
const username = ref('');
const password = ref('');
const rememberUser = ref(false); // 

// 2. Vanta
const vantaRef = ref(null);
const vantaEffect = ref(null);

// 3. 
const showText = ref(false);
const TextContent = ref('');
const InfoIcon = ref('');
const currentAnimation = ref('animate__fadeInDown');
const textRef = ref(null);
let hideTimer = null;

// ------------------------------
// 
// ------------------------------
// localStorage
onMounted(() => {
  // Vanta
  if (vantaRef.value) {
    vantaEffect.value = NET({
      el: vantaRef.value,
      THREE: THREE,
      mouseControls: true,
      touchControls: true,
      gyroControls: false,
      minHeight: window.innerHeight,
      minWidth: window.innerWidth,
      scale: 0.5,
      scaleMobile: 1.00,
      color: "#ffffff",
      backgroundColor: "#c0c0c0",
      maxDistance: 21.00,
    })
  }

  // localStorage
  const savedUser = localStorage.getItem('savedUsername');
  const savedPwd = localStorage.getItem('savedPassword');
  const isRemembered = localStorage.getItem('isRemembered') === 'true'; // 

  // 
  if (savedUser && isRemembered) {
    username.value = savedUser;
    password.value = savedPwd || ''; // 
    rememberUser.value = true; // 
  }
});

// Vanta
onBeforeUnmount(() => {
  if (vantaEffect.value) {
    vantaEffect.value.destroy();
  }
  // 
  if (hideTimer) clearTimeout(hideTimer);
});

// ------------------------------
// 
// ------------------------------
const login = async () => {
  // console.log("", username.value, " ", password.value)

  try {
    const response = await axiosInstance.post('/sysUser/login', {
      username: username.value,
      password: password.value
    });

    if (response.data.code === 0) {
      // 1. rememberUser
      if (rememberUser.value) {
        // localStorage
        localStorage.setItem('savedUsername', username.value);
        localStorage.setItem('savedPassword', password.value); // 
        localStorage.setItem('isRemembered', 'true'); // 
      } else {
        // 
        localStorage.removeItem('savedUsername');
        localStorage.removeItem('savedPassword');
        localStorage.removeItem('isRemembered');
      }

      // 2. 
      const loginTime = new Date().getTime();
      localStorage.setItem('userid', response.data.data);
      localStorage.setItem('loginTime', loginTime);
      localStorage.setItem('currentTime', loginTime);

      console.log('id',response.data.data);

      // 3. 
      router.push({ path: '/BDDSPage', query: { userid: response.data.data } });
    } else {
      handleIrregularInfo(response.data.code, response.data.message);
    }
  } catch (error) {
    // console.log(error);
    showTip('pi pi-exclamation-circle', '');
  }
};

// ------------------------------
// 
// ------------------------------
// 
const handleforgetPassword = () => {
  showTip('pi pi-question-circle', '');
};

// 
const handleIrregularInfo = (code, msg) => {
  let icon = 'pi pi-info-circle';
  let text = msg || '';
  
  switch (code) {
    case 3001:
      console.log('');
      break;
    case 3002:
      console.log('');
      break;
    case 3003:
      console.log('');
      break; // 
    default:
      text = '';
  }
  
  showTip(icon, text);
};

// 
const showTip = (icon, text) => {
  showText.value = true;
  InfoIcon.value = icon;
  TextContent.value = text;

  // 
  if (hideTimer) clearTimeout(hideTimer);

  // 2
  hideTimer = setTimeout(() => {
    currentAnimation.value = 'animate__fadeOutDown';
    setTimeout(() => {
      showText.value = false;
    }, 1000); // 
  }, 2000);
};

// 
watch(showText, (newVal) => {
  if (newVal) {
    currentAnimation.value = 'animate__fadeInDown';
  }
});
</script>

<style>
@layer reset, primevue, custom;

@layer custom {
  /*  */
  .vanta-container {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }

  /*  */
  .login-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    height: auto;
    padding: 1rem 0 3rem;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  /*  */
  .login-container {
    --filter-glass3d: blur(1px) brightness(1) saturate(1);
    --color-glass3d: transparent;
    --noise-glass3d: url("https://www.transparenttextures.com/patterns/egg-shell.png");
    position: relative;
    z-index: 4;
    box-shadow:
      0 0 0.75px hsl(205 20% 10% / 0.2),
      0.7px 0.8px 1.2px -0.4px hsl(205 20% 10% / 0.1),
      1.3px 1.5px 2.2px -0.8px hsl(205 20% 10% / 0.1),
      2.3px 2.6px 3.9px -1.2px hsl(205 20% 10% / 0.1),
      3.9px 4.4px 6.6px -1.7px hsl(205 20% 10% / 0.1),
      6.5px 7.2px 10.9px -2.1px hsl(205 20% 10% / 0.1),
      8px 9px 14px -2.5px hsl(205 20% 10% / 0.2);
  }

  .login-container::before,
  .login-container::after {
    content: "";
    position: absolute;
    inset: 0;
    pointer-events: none;
    border-radius: inherit;
    overflow: hidden;
  }

  .login-container::before {
    z-index: 3;
    -webkit-backdrop-filter: var(--filter-glass3d);
    backdrop-filter: var(--filter-glass3d);
    background-color: var(--color-glass3d);
    background-image: var(--noise-glass3d);
    background-size: 100px;
    background-repeat: repeat;
  }

  .login-container::after {
    z-index: 5;
    box-shadow:
      inset 2px 2px 1px -3px hsl(205 20% 90% / 0.8),
      inset 4px 4px 2px -6px hsl(205 20% 90% / 0.3),
      inset 1.5px 1.5px 1.5px -0.75px hsl(205 20% 90% / 0.15),
      inset 1.5px 1.5px 0.25px hsl(205 20% 90% / 0.03),
      inset 0 0 0.25px 0.5px hsl(205 20% 90% / 0.03);
  }

  .login-container>* {
    position: relative;
    z-index: 6;
  }

  /*  */
  .header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.6rem;
    color: rgb(31, 31, 32);
    width: 100%;
    height: 7rem;
    padding-top: 1.12rem;
  }

  .header img {
    width: 20px;
    height: 20px;
  }

  .header .title {
    font-size: 1.1em;
    font-weight: bold;
  }

  .header .sub-title {
    font-size: 0.8em;
  }

  /*  */
  .body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 14px;
    width: 230px;
  }

  .body .form-group {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  .form-group .form-lable {
    font-size: 14px;
    margin-bottom: 6px;
    color: rgb(31, 31, 32);
  }

  .form-group .form-input {
    width: 100%;
    padding: 10px;
    border-radius: 8px;
    border: none;
    font-size: 16px;
    color: rgb(31, 31, 32);
    box-sizing: border-box; /* padding */
  }

  /*  */
  .form-operation {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
  }

  /*  */
  .info-container {
    width: 100%;
    height: 1.5rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.7rem;
  }

  /* ------------------------------
     
     ------------------------------ */
  .remember-wrapper {
    display: flex;
    align-items: center;
    gap: 0.3rem;
    margin-bottom: 1rem;
  }

  .remember-label {
    font-size: 12px;
    color: rgb(31, 31, 32);
    letter-spacing: 1px;
    margin: 0;
    line-height: 1;
    cursor: pointer; /*  */
  }
}
</style>